#wpradio-page .sk-cube-grid {
  margin: 100px auto;
}
#wpradio-page .sk-cube-grid .title {
  position: relative;
  bottom: 0;
  top: 10px;
  color: #6035bd;
  font-size: 1.2em;
  font-weight: 600;
  text-align: center;
}
#wpradio-page .sk-cube-grid .sk-cube {
  width: 33%;
  height: 33%;
  background-color: #6035bd;
  float: left;
  -webkit-animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out;
  animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out;
}
#wpradio-page .sk-cube-grid .sk-cube1 {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s;
  border-top-left-radius: 4px;
}
#wpradio-page .sk-cube-grid .sk-cube2 {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s;
}
#wpradio-page .sk-cube-grid .sk-cube3 {
  -webkit-animation-delay: 0.4s;
  animation-delay: 0.4s;
  border-top-right-radius: 4px;
}
#wpradio-page .sk-cube-grid .sk-cube4 {
  -webkit-animation-delay: 0.1s;
  animation-delay: 0.1s;
}
#wpradio-page .sk-cube-grid .sk-cube5 {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s;
}
#wpradio-page .sk-cube-grid .sk-cube6 {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s;
}
#wpradio-page .sk-cube-grid .sk-cube7 {
  -webkit-animation-delay: 0s;
  animation-delay: 0s;
  border-bottom-left-radius: 4px;
}
#wpradio-page .sk-cube-grid .sk-cube8 {
  -webkit-animation-delay: 0.1s;
  animation-delay: 0.1s;
}
#wpradio-page .sk-cube-grid .sk-cube9 {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s;
  border-bottom-right-radius: 4px;
}
@-webkit-keyframes sk-cubeGridScaleDelay {
  0%, 70%, 100% {
    opacity: 0.85;
    -webkit-transform: scale3D(1, 1, 1);
    transform: scale3D(1, 1, 1);
  }
  35% {
    opacity: 0;
    -webkit-transform: scale3D(0, 0, 1);
    transform: scale3D(0, 0, 1);
  }
}
@keyframes sk-cubeGridScaleDelay {
  0%, 70%, 100% {
    opacity: 0.85;
    -webkit-transform: scale3D(1, 1, 1);
    transform: scale3D(1, 1, 1);
  }
  35% {
    opacity: 0;
    -webkit-transform: scale3D(0, 0, 1);
    transform: scale3D(0, 0, 1);
  }
}
#wpradio-page .wpradiospinner {
  position: relative;
  margin: 100px auto;
}
#wpradio-page .wpradiospinner .double-bounce1, #wpradio-page .wpradiospinner .double-bounce2 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #6035bd;
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: sk-bounce 2s infinite ease-in-out;
  animation: sk-bounce 2s infinite ease-in-out;
}
#wpradio-page .wpradiospinner .double-bounce2 {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}
#wpradio-page .wpradiospinner .title {
  position: relative;
  bottom: 0;
  top: 76px;
  color: #6035bd;
  font-size: 1.2em;
  font-weight: 600;
  text-align: center;
}
@-webkit-keyframes sk-bounce {
  0%, 100% {
    -webkit-transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1);
  }
}
@keyframes sk-bounce {
  0%, 100% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }
  50% {
    transform: scale(1);
    -webkit-transform: scale(1);
  }
}

